import Text from "components/Text";

import { Tooltip, TooltipProps } from "primereact/tooltip";
import React from "react";
import { stringToHTML } from "utils/CommonUtil";
import { INPUT_STYLE_TYPE } from "utils/Constant";

interface ITooltip extends TooltipProps {
  htmlContent?: string;
  variant?: string;
}

const SimpleToolTipWithoutMemo: React.FC<ITooltip> = (props: ITooltip) => {
  const { content, htmlContent, variant, ...primeReactProps } = props;
  const { target, position, className } = primeReactProps;

  // bg-black text-white

  const variantClassName =
    variant === INPUT_STYLE_TYPE?.PRIMARY ? " tooltip-primary " : "";
  const stringToHtml = "";

  const renderContent = () => {
    if (htmlContent) {
      return <div dangerouslySetInnerHTML={{ __html: htmlContent }} />;
    }
    return content;
  };

  return (
    <React.Fragment>
      {/* <Tooltip
        target={target}
        position={position}
        className={className ?? ""}
        autoHide={false}
        {...primeReactProps}
      /> */}

      <Tooltip
        {...primeReactProps}
        target={target}
        position={position}
        className={` !p-0 ${variantClassName??""} ${className??""}  ` ?? ""}
        autoHide={false}
      
      >
        {renderContent()}
      </Tooltip>
      
    </React.Fragment>
  );
};

// ----------- memoization -------------------- //
const SimpleToolTip = React.memo(SimpleToolTipWithoutMemo);

// ----------- redux wiring ------------------- //

// ----------- exports ------------------------ //
export default SimpleToolTip;
